<template>
  <div style="height: 100%" class="sflex">
    <g-body-form-layout>
      <template #search-card>
        <div class="search-card">
          <a-form-model ref="form" :model="entity" autocomplete="off" layout="vertical">
            <a-row :gutter="24">
              <a-col :sm="4">
                <!-- 批次名称 -->
                <a-form-model-item
                  :name="['containerName']"
                  :rules="{
                    required: !entity.isUseNumberingRule,
                    message: l('必填'),
                  }"
                  :label="l('ContainerName')"
                  prop="containerName"
                  ref="containerName"
                >
                  <a-input
                    name="containerName"
                    v-model:value="entity.containerName"
                    :placeholder="l('ContainerName')"
                    required
                    :maxlength="200"
                    pattern="^[^ ]+$"
                    :disabled="entity.isUseNumberingRule"
                  />
                </a-form-model-item>
                <a-form-model-item :name="['isUseNumberingRule']" label="" prop="isUseNumberingRule" ref="isUseNumberingRule">
                  <a-checkbox
                    v-model:checked="entity.isUseNumberingRule"
                    @changeValue="valueChange()"
                    >{{ l("AutoGenerate") }}</a-checkbox
                  >
                </a-form-model-item>
              </a-col>

              <a-col :sm="4">
                <!-- 批次等级 -->
                <a-form-model-item
                  :name="['levelId']"
                  :rules="{ required: true, message: l('必填') }"
                  :label="l('ContainerLevel')"
                  prop="levelId"
                  ref="levelId"
                >
                  <g-select
                    name="levelId"
                    required
                    dataSource="GCT.MedPro.Core.ContainerModule.ContainerLevelDef.Entity.ContainerLevel"
                    v-model:value="entity.levelId"
                  ></g-select>
                </a-form-model-item>
              </a-col>
              <a-col :sm="4">
                <!-- 工单 -->
                <a-form-model-item :label="l('MfgOrder')">
                  <g-select
                    name="mfgOrderId"
                    dataSource="GCT.MedPro.Core.MfgOrderModule.MfgOrderService.MfgOrder"
                    v-model:value="entity.mfgOrderId"
                  >
                  </g-select>
                </a-form-model-item>
              </a-col>
              <a-col :sm="4">
                <a-form-model-item :label="l('Factory')">

                <g-select
                    dataSource="GCT.MedPro.Core.OrganizationModule.FactoryDefinition.Factory"
                    name="factoryIdSelectRdo"
                    v-model:value="entity.factoryId"
                  >
                  </g-select>
                  </a-form-model-item>
              </a-col>
              <a-col :sm="4">
                <a-form-model-item
                  :name="['qty']"
                  :rules="{ required: true, message: l('必填') }"
                  :label="l('Qty')"
                  prop="qty"
                  ref="qty"
                >
                  <g-number-input>
                    <a-input-number
                      name="changeQty"
                      v-model:value="entity.qty"
                      style="width: 100%"
                      required
                      :placeHolder="l('Qty')"
                      :min="0"
                      :step="1"
                      :precision="4"
                      precisionMode="cut"
                    />
                    <!-- <validation-messages [formCtrl]="qty"></validation-messages> -->
                  </g-number-input>
                </a-form-model-item>
              </a-col>

              <a-col :sm="4">
                <a-form-model-item :label="l('UOM')">
                  <g-select
                    name="uomId"
                    dataSource="GCT.MedPro.Core.UomModule.UOMDefinition.UOM"
                    v-model:value="entity.uomId"
                  >
                  </g-select>
                </a-form-model-item>
              </a-col>

              <!-- 下拉选择工厂，后端的dto 添加一个id字段  然后重新生成service-proxy -->
              <a-col :sm="4">
                 

                   <!-- 产品 -->
                <a-form-model-item
                  :name="['product']"
                  :rules="{ required: true, message: l('必填') }"
                  :label="l('Product')"
                  prop="product"
                  ref="product"
                >
                  <g-rdo-select
                    name="productInput"
                    v-model:value="entity.product"
                    required
                    dataSource="GCT.MedPro.Core.ProductModule.ProductDefinition.Entity.Product"
                    @change="productValueChange($event)"
                  >
                  </g-rdo-select>
                  <!-- <validation-messages [formCtrl]="productInput"></validation-messages> -->
                </a-form-model-item>
              </a-col>
              <a-col :sm="4">
                <a-form-model-item
                  :name="['workFlow']"
                  :rules="{ required: true, message: l('必填') }"
                  :label="l('Workflow')"
                  prop="workFlow"
                  ref="workFlow"
                >
                  <g-rdo-select
                    name="workFlowInput"
                    ref="workflow"
                    v-model:value="entity.workFlow"
                    :params="{ id: 1 }"
                    dataSource="GCT.MedPro.Core.WorkFlowModule.WorkflowDefinition.Entity.Workflow"
                    @change="workFlowValueChange($event)"
                  >
                  </g-rdo-select>
                </a-form-model-item>
              </a-col>
            </a-row>
          </a-form-model>
        </div>
      </template>

      <template #g-fixed-row>
        <g-fixed-row>
          <div class="g-fixed-row">
            <!-- icon="gctzhongzhi" -->
            <g-btn
              size="large"
              :text="l('Reset')"
              @callback="refresh()"
              class="btn-margin-right-12"
            >
            </g-btn>
            <!-- icon="gctkeep" -->
            <g-btn
              type="primary"
              size="large"
              :text="l('Submit')"
              @callback="save()"
            ></g-btn>
          </div>
        </g-fixed-row>
      </template>
    </g-body-form-layout>
  </div>
</template>

<script >
import { defineComponent } from 'vue';


export default defineComponent({
  name: 'container-start',
  data() {
    return {
      product: [], // 产品
      workFlow: [], // 工作流
      entity:{},
      _containerServiceProxy: {},
      _productServiceProxy:{},
      form: {},
      filepath: ''
    };
  },
  created() {
  
  },
  methods: {
    l(str) {
        return str;
    },
    /**
     * 保存方法,提交form表单
     */
    save() {
      this.$refs.form.validate().then(() => {
       
      });
    },
    // 重置数据
    refresh() {
      
    },
    /**
     * 产品下拉回调
     * @param value
     */
    productValueChange(value) {
     
    },
    /**
     * 工作流下拉回调
     * @param value
     */
    workFlowValueChange(value) {
     
    },
    /**
     * 批次名称清空
     */
    valueChange() {
      this.entity.containerName = '';
    }
  }
});
</script>

<style lang="less" scoped>
/deep/ .ant-form-item {
  margin-bottom: 0 !important;
}
</style>